"use client";
import React, { useState, useEffect } from "react";
import BackArrow from "@/app/components/ui/BackArrow";
import Button from "@/app/components/ui/Button";
import { COLORS, TEXT_STYLES } from "@/app/constants";
import Input from "@/app/components/ui/Input";

const OtpPage = () => {
  const [otp, setOtp] = useState(["", "", "", ""]);
  const [timer, setTimer] = useState(150); // 2:30 in seconds

  // countdown
  useEffect(() => {
    if (timer <= 0) return;
    const interval = setInterval(() => setTimer((t) => t - 1), 1000);
    return () => clearInterval(interval);
  }, [timer]);

  const handleChange = (value: string, index: number) => {
    if (/^[0-9]?$/.test(value)) {
      const newOtp = [...otp];
      newOtp[index] = value;
      setOtp(newOtp);

      // auto focus next input
      if (value && index < otp.length - 1) {
        const next = document.getElementById(`otp-${index + 1}`);
        next?.focus();
      }
    }
  };

  const formatTime = (seconds: number) => {
    const m = Math.floor(seconds / 60);
    const s = seconds % 60;
    return `${m}:${s.toString().padStart(2, "0")}`;
  };

  return (
    <div className="py-20 mx-auto max-w-200 relative h-screen flex flex-col justify-between px-4">
      {/* Back Arrow */}
      <div className="flex flex-col gap-8 ">
        <BackArrow />
        {/* Headline */}
        <Input
          headline="تغيير كلمة المرور؟"
          placeholder="ادخل الرمز التعريفي المرسل الي البريد الالكتروني"
          isInput={false}
          isPassInput={false}
        />

        {/* OTP Inputs */}
        <div className="flex justify-center gap-4 mt-8">
          {otp.map((digit, i) => (
            <input
              key={i}
              id={`otp-${i}`}
              type="text"
              maxLength={1}
              value={digit}
              onChange={(e) => handleChange(e.target.value, i)}
              className="w-14 h-14 text-center border-2 border-gray-300 rounded-md text-xl outline-none focus:border-green-500"
            />
          ))}
        </div>

        {/* Resend + Timer */}
        <div className="flex justify-between mt-4 text-sm text-gray-600">
          <span>{formatTime(timer)}</span>
          <button
            className="text-green-600 underline disabled:text-gray-400"
            disabled={timer > 0}
            onClick={() => setTimer(150)}
          >
            اعِد ارسال الرمز التعريفي
          </button>
        </div>
      </div>

      {/* Submit Button */}
      <div className="mt-auto mb-6">
        <Button
          isMain
          text="ارسال"
          onClick={() => console.log("OTP:", otp.join(""))}
        />
      </div>
    </div>
  );
};

export default OtpPage;
