"use client";

import { COLORS, SHADOWS, TEXT_STYLES } from "@/app/constants";
import React, { useState } from "react";
import { FaEye, FaEyeSlash } from "react-icons/fa";

interface InputProps {
  headline: string;
  placeholder: string;
  onChangeText?: (text: string) => void;
  isPassInput: boolean;
  isInput: boolean;
}

const Input: React.FC<InputProps> = ({
  headline,
  placeholder,
  onChangeText,
  isPassInput,
  isInput,
}) => {
  const [showPassword, setShowPassword] = useState(false);

  return (
    <div className="flex flex-col items-end gap-3">
      <label
        style={{
          ...(isInput
            ? TEXT_STYLES.READEX_PRO.SEMIBOLD.H5
            : TEXT_STYLES.READEX_PRO.SEMIBOLD.H4),
          color: COLORS.TEXT,
        }}
        className="text-sm"
      >
        {headline}
      </label>

      <div
        className={`rounded-md flex items-center px-4 gap-2 ${
          isInput ? "w-full" : ""
        }`}
        style={isInput ? { boxShadow: SHADOWS.DROP } : {}}
      >
        {isPassInput && isInput && (
          <button
            type="button"
            onClick={() => setShowPassword((prev) => !prev)}
            className="cursor-pointer text-gray-500"
          >
            {showPassword ? <FaEyeSlash /> : <FaEye />}
          </button>
        )}

        {isInput ? (
          <input
            style={{ ...TEXT_STYLES.READEX_PRO.MEDIUM.H7, color: COLORS.GRAY }}
            className="py-3 text-right flex-1 outline-none bg-transparent"
            onChange={(e) => onChangeText(e.target.value)}
            placeholder={placeholder}
            type={isPassInput ? (showPassword ? "text" : "password") : "text"}
          />
        ) : (
          <div
            style={{ ...TEXT_STYLES.READEX_PRO.MEDIUM.H7, color: COLORS.GRAY }}
            className="text-right"
          >
            {placeholder}
          </div>
        )}
      </div>
    </div>
  );
};

export default Input;
